"use client";

import { motion } from 'framer-motion';
import {
    PlusIcon,
    SearchIcon,
    FilterIcon,
    MoreVerticalIcon,
    PencilIcon,
    Trash2Icon,
    ExternalLinkIcon
} from 'lucide-react';
import Image from 'next/image';
import { useState } from 'react';

// Sample Product Data (using a subset of legacy data)
const initialProducts = [
    {
        "id": "gen-surgical-drape-pack",
        "name": "General Surgical Drape Pack",
        "category": "Surgery Packs",
        "stock": "422",
        "status": "In Stock",
        "image": "https://images.unsplash.com/photo-1579684385127-1ef15d508118?auto=format&fit=crop&q=80&w=800"
    },
    {
        "id": "ortho-drape-pack",
        "name": "Ortho Drape Pack",
        "category": "Orthopedic",
        "stock": "128",
        "status": "In Stock",
        "image": "https://images.unsplash.com/photo-1631815541542-e8fc9529598e?auto=format&fit=crop&q=80&w=800"
    },
    {
        "id": "gown-sms-reinforced",
        "name": "Reinforced Surgeon Gown",
        "category": "Apparel",
        "stock": "14",
        "status": "Low Stock",
        "image": "https://images.unsplash.com/photo-1584622650111-993a426fbf0a?auto=format&fit=crop&q=80&w=800"
    }
];

export default function AdminProductsPage() {
    const [products] = useState(initialProducts);

    return (
        <div className="space-y-8">
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <div>
                    <h1 className="text-4xl font-black text-slate-900 tracking-tighter uppercase mb-1">
                        Product <span className="text-medical-600">Archive</span>
                    </h1>
                    <p className="text-slate-500 text-sm font-medium uppercase tracking-widest">Storefront Inventory Management</p>
                </div>
                <button className="bg-medical-700 hover:bg-medical-900 text-white px-8 py-4 rounded-xl text-sm font-black uppercase tracking-widest transition-all flex items-center gap-2 shadow-xl shadow-medical-700/20 active:scale-95">
                    <PlusIcon className="w-5 h-5" />
                    Add New Product
                </button>
            </div>

            {/* Toolbar */}
            <div className="bg-white p-4 rounded-2xl border border-slate-100 flex flex-col md:flex-row gap-4 items-center">
                <div className="flex-1 relative w-full">
                    <SearchIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                    <input
                        type="text"
                        placeholder="Search products by name or category..."
                        className="w-full bg-slate-50 border-none rounded-xl pl-12 pr-4 h-12 text-sm focus:ring-2 focus:ring-medical-500/20 transition-all font-medium"
                    />
                </div>
                <div className="flex gap-2 w-full md:w-auto">
                    <button className="flex-1 md:flex-none h-12 px-6 rounded-xl bg-slate-50 text-slate-600 text-xs font-black uppercase tracking-widest flex items-center justify-center gap-2 hover:bg-slate-100 transition-all">
                        <FilterIcon className="w-4 h-4" />
                        Filters
                    </button>
                </div>
            </div>

            {/* Product Table */}
            <div className="bg-white rounded-[2.5rem] border border-slate-100 overflow-hidden shadow-sm">
                <table className="w-full text-left border-collapse">
                    <thead>
                        <tr className="bg-slate-50/50 border-b border-slate-100">
                            <th className="px-8 py-5 text-[10px] font-black uppercase tracking-widest text-slate-400">Product Entry</th>
                            <th className="px-8 py-5 text-[10px] font-black uppercase tracking-widest text-slate-400">Category</th>
                            <th className="px-8 py-5 text-[10px] font-black uppercase tracking-widest text-slate-400">Inventory</th>
                            <th className="px-8 py-5 text-[10px] font-black uppercase tracking-widest text-slate-400 text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-slate-50">
                        {products.map((product) => (
                            <motion.tr
                                key={product.id}
                                initial={{ opacity: 0 }}
                                animate={{ opacity: 1 }}
                                className="hover:bg-slate-50/50 transition-colors group"
                            >
                                <td className="px-8 py-6">
                                    <div className="flex items-center gap-4">
                                        <div className="relative w-12 h-12 rounded-xl overflow-hidden bg-slate-100 flex-shrink-0">
                                            <Image src={product.image} alt={product.name} fill className="object-cover" />
                                        </div>
                                        <div>
                                            <div className="font-black text-slate-900 uppercase tracking-tight leading-none mb-1">
                                                {product.name}
                                            </div>
                                            <div className="text-[10px] font-bold text-slate-400 uppercase tracking-widest">ID: {product.id}</div>
                                        </div>
                                    </div>
                                </td>
                                <td className="px-8 py-6">
                                    <span className="text-[10px] font-black uppercase tracking-widest text-medical-700 bg-medical-50 px-2.5 py-1 rounded-full">
                                        {product.category}
                                    </span>
                                </td>
                                <td className="px-8 py-6">
                                    <div className="flex flex-col gap-1">
                                        <span className="font-bold text-slate-900">{product.stock} units</span>
                                        <span className={cn(
                                            "text-[10px] font-black uppercase tracking-widest",
                                            product.status === 'Low Stock' ? 'text-rose-500' : 'text-emerald-500'
                                        )}>
                                            {product.status}
                                        </span>
                                    </div>
                                </td>
                                <td className="px-8 py-6">
                                    <div className="flex items-center justify-end gap-2">
                                        <button className="p-2.5 rounded-lg text-slate-400 hover:text-medical-600 hover:bg-medical-50 transition-all">
                                            <PencilIcon className="w-5 h-5" />
                                        </button>
                                        <button className="p-2.5 rounded-lg text-slate-400 hover:text-slate-900 hover:bg-slate-100 transition-all">
                                            <ExternalLinkIcon className="w-5 h-5" />
                                        </button>
                                        <button className="p-2.5 rounded-lg text-slate-400 hover:text-red-500 hover:bg-red-50 transition-all">
                                            <Trash2Icon className="w-5 h-5" />
                                        </button>
                                    </div>
                                </td>
                            </motion.tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

import { cn } from '@/lib/utils';
